import React, { useState } from "react";
import Header from "../components/Header/Header";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { NavLink } from "react-router-dom";
import { useEffect } from "react";
import axios from "axios";
import Country from "../components/country/Country";
import coverTioby from "../assets/images/cover.png";

const schemaValidator = yup
  .object({
    firstname: yup.string().required(),
    lastname: yup.string().required(),
    email: yup.string().email("Email not valid").required(),
    adresse: yup.string().required(),
    telephone: yup.string().required(),
    password: yup.string().required(),
    cfpassword: yup.string().required(),
    country: yup.string().required(),
  })
  .required();

export default function Register({ navigation }) {
  const {
    register: registerForm,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schemaValidator),
  });

  const onSubmit = (data) => console.log("submit: ", data);
  // console.log(watch("data: ")); // watch input value by passing the name of it

  useEffect(() => {
    axios
      .get("https://api.shippex.net/get/infosCountry")
      .then((data) => console.log(data));
  }, []);

  return (
    <div className="cool-bg">
      <Header />

      {/* <!-- Section: Design Block --> */}
      <section className="">
        {/* <!-- Jumbotron --> */}
        <div
          className="px-4 py-5 px-md-5 text-center text-lg-start"
          style={{ backgroundColor: "hsl(0, 0%, 96%)" }}
        >
          <div className="container">
            <div class="row d-flex align-items-center justify-content-center h-100">
              <div class="col-md-8 col-lg-7 col-xl-6">
                <img src={coverTioby} class="img-fluid" />
              </div>

              <div class="col-md-8 col-lg-7 col-xl-6">
                <div className="card">
                  <h1 className="text-center">Register</h1>
                  <div className="card-body py-5 px-md-5">
                    <form
                      action=""
                      method="POST"
                      onSubmit={handleSubmit(onSubmit)}
                    >
                      {/* <!-- 2 column grid layout with text inputs for the first and last names --> */}
                      <div className="row">
                        <div className="col-md-6 ">
                          <div className="form-outline">
                            <input
                              type="text"
                              id="firstname"
                              className="form-control"
                              name="firstname"
                              {...registerForm("firstname")}
                            />
                            <label className="form-label" htmlFor="firstname">
                              First name
                            </label>
                          </div>
                          <p className="text-danger text-center">
                            {errors.firstname?.message}
                          </p>
                        </div>
                        <div className="col-md-6">
                          <div className="form-outline">
                            <input
                              type="text"
                              id="lastname"
                              className="form-control"
                              name="lastname"
                              {...registerForm("lastname")}
                            />
                            <label className="form-label" htmlFor="lastname">
                              Last name
                            </label>
                          </div>
                          <p className="text-danger text-center">
                            {errors.lastname?.message}
                          </p>
                        </div>
                      </div>

                      {/* <!-- Email input --> */}
                      <div className="form-outline mb-3">
                        <input
                          type="email"
                          id="email"
                          className="form-control"
                          name="email"
                          {...registerForm("email")}
                        />
                        <label className="form-label" htmlFor={"email"}>
                          Email address
                        </label>
                        <p className="text-danger text-center">
                          {errors.email?.message}
                        </p>
                      </div>
                      {/* <!-- Address input --> */}
                      <div className="form-outline mb-3">
                        <input
                          type="text"
                          id="adresse"
                          className="form-control"
                          name="adresse"
                          {...registerForm("adresse")}
                        />
                        <label className="form-label" htmlFor={"adresse"}>
                          Address
                        </label>
                        <p className="text-danger text-center">
                          {errors.adresse?.message}
                        </p>
                      </div>

                      {/* <!-- Telephone input --> */}
                      <div className="form-outline mb-3">
                        <input
                          type="text"
                          id="telephone"
                          className="form-control"
                          name="telephone"
                          {...registerForm("telephone")}
                        />
                        <label className="form-label" htmlFor={"telephone"}>
                          Telephone
                        </label>
                        <p className="text-danger text-center">
                          {errors.telephone?.message}
                        </p>
                      </div>

                      {/* <!-- Password input --> */}
                      <div className="form-outline mb-3">
                        <input
                          type="password"
                          id="password"
                          className="form-control"
                          name="password"
                          {...registerForm("password")}
                        />
                        <label className="form-label" htmlFor={"password"}>
                          Password
                        </label>
                        <p className="text-danger text-center">
                          {errors.password?.message}
                        </p>
                      </div>
                      {/* <!-- Confirm Password input --> */}
                      <div className="form-outline mb-3">
                        <input
                          type="password"
                          id="cfpassword"
                          className="form-control"
                          name="cfpassword"
                          {...registerForm("cfpassword")}
                        />
                        <label className="form-label" htmlFor={"cfpassword"}>
                          Confirm Password
                        </label>
                        <p className="text-danger text-center">
                          {errors.cfpassword?.message}
                        </p>
                      </div>

                      {/* <!-- country --> */}
                      <div className="form-outline mb-3">
                        <Country {...registerForm("country")} />
                        <p>{errors.country?.message}</p>
                      </div>

                      {/* <!-- Submit button --> */}
                      <div className="d-flex">
                        <button
                          type={"submit"}
                          name="register"
                          style={{ width: "150px", marginRight: "auto" }}
                          className="btn btn-primary btn-block mb-3 "
                        >
                          S'enregistrer
                        </button>
                        <NavLink exact to={"/login"}>
                          <div
                            className="btn btn-outline-secondary btn-block text-dark mb-3  "
                            style={{ width: "150px" }}
                          >
                            Connection
                          </div>
                        </NavLink>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <!-- Jumbotron --> */}
      </section>
      {/* <!-- Section: Design Block --> */}
    </div>
  );
}
