import React, { useState } from "react";
import Header from "../components/Header/Header";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { NavLink } from "react-router-dom";
import coverTioby from "../assets/images/cover.png";
const schema = yup
  .object({
    email: yup.string().email("Email not valid").required(),
    password: yup.string().required(),
  })
  .required();

export default function Login({ navigation }) {
  const [thnxID, setThnxID] = useState("");

  const {
    register: loginForm,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });
  const onSubmit = (data) => console.log("submit: ", data);

  return (
    <div className="cool-bg">
      <Header />

      {/* <!-- Section: Design Block --> */}
      <section className="">
        {/* <!-- Jumbotron --> */}
        <div
          className="px-4 py-5 px-md-5 text-center text-lg-start"
          style={{ backgroundColor: "hsl(0, 0%, 96%)" }}
        >
          <div className="container">
            <div class="row d-flex align-items-center justify-content-center h-100">
              <div class="col-md-8 col-lg-7 col-xl-6">
                <img src={coverTioby} class="img-fluid" alt="Phone image" />
              </div>
              <div className="col-lg-6 mb-5 mb-lg-0">
                <div className="card">
                  <div className="card-body py-5 px-md-5">
                    <form
                      action=""
                      method="POST"
                      onSubmit={handleSubmit(onSubmit)}
                    >
                      {/* <!-- Email input --> */}
                      <div className="form-outline mb-4">
                        <input
                          type="email"
                          id="email"
                          className="form-control"
                          name="email"
                          {...loginForm("email")}
                        />
                        <label className="form-label" htmlFor={"email"}>
                          Email address
                        </label>
                        <p>{errors.email?.message}</p>
                      </div>

                      {/* <!-- Password input --> */}
                      <div className="form-outline mb-4">
                        <input
                          type="password"
                          id="password"
                          className="form-control"
                          name="password"
                          {...loginForm("password")}
                        />
                        <label className="form-label" htmlFor={"password"}>
                          Password
                        </label>
                        <p>{errors.email?.message}</p>
                      </div>

                      {/* <!-- Submit button --> */}
                      <div className="d-flex">
                        <button
                          type={"submit"}
                          name="login"
                          style={{ width: "150px", marginRight: "auto" }}
                          className="btn btn-primary btn-block mb-4 "
                        >
                          S'enregistrer
                        </button>

                        <NavLink exact to={"/register"}>
                          <div
                            className="btn btn-outline-secondary btn-block text-dark mb-3  "
                            style={{ width: "150px" }}
                          >
                            Register
                          </div>
                        </NavLink>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <!-- Jumbotron --> */}
      </section>
      {/* <!-- Section: Design Block --> */}
    </div>
  );
}
