import { PayPalScriptProvider } from "@paypal/react-paypal-js";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./App.css";
import Albums from "./pages/Albums";
import Payment from "./pages/Payment";
import SidebarContextProvider from "./context/SidebarContext";
import axios from "axios";
import Login from "./pages/Login";
import Register from "./pages/Register";
function App() {
  const initialOptions = {
    // disableFunding: 'credit',
    "client-id":
      "AdztWsDvxDqKDmVaxLc-IUUH3LPuCMQ4VRrIGP9qCsWyUvhuoyOQyg8fZU_pSMJxfDN8EQjBCC4LGnKN",
    currency: "USD",
    intent: "capture",
    // "data-client-token":
    //   "EBzBsXmU4tfOQ86ZQgodxAbEzuDcZ9YioTJSNq0mIO1xikXruCsoTIvJvwMiB443NSREaa44isFvJIMx",
  };
  const user = null;
  
  (function () {
    if (user != null && user.token) {
      axios.defaults.headers.common["Authorization"] = `Bearer ${user.token}`;
      let host = "http://localhost/plongayeapi";
      axios.defaults.baseURL = host;
    } else {
      let host = "http://localhost/plongayeapi";
      axios.defaults.baseURL = host;
      axios.defaults.headers.common["Authorization"] = null;
    }
  })();

  return (
    <>
      <PayPalScriptProvider options={initialOptions} deferLoading={true}>
        <SidebarContextProvider>
          <Router>
            <Switch>
              <Route path="/login" exact component={Login}></Route>
              <Route path="/register" exact component={Register}></Route>
              <Route path="/payment" exact component={Payment}></Route>
              <Route path="/albums" exact component={Albums}></Route>
            </Switch>
          </Router>
        </SidebarContextProvider>
      </PayPalScriptProvider>
    </>
  );
}

export default App;
