import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";

export default function ModalForm({
  show,
  handleClose,
  setThnxID,
  handleShow,
}) {
  //* To re-render only the modal when user type
  const [inputModal, setInputModal] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();

    if (event.target[1].value) {
      setThnxID(inputModal);
      handleClose();
    }
  };

  return (
    <>
      <Modal show={show} onHide={handleClose} centered>
        <Form onSubmit={handleSubmit}>
          <Modal.Header closeButton>
            <Modal.Title>Transaction ID</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Group className="mb-3" controlId="thnxId">
              <Form.Label>Enter your MonCash TxID</Form.Label>
              <Form.Control
                type="text"
                placeholder="1234"
                onChange={(userInput) => setInputModal(userInput.target.value)}
                autoFocus
                required
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" type="submit">
              Submit
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
}
